<template>
    <div id='login'>
        <Card class="login-card">

            <div class="login-title">
                <p>XX管理系统</p>
            </div>
            <div class="login-form">
                <Form :model="formCustom" :rules="rules" ref="loginForm">
                    <!-- 用户名 -->
                    <FormItem prop="nickname">
                        <Input v-model='formCustom.nickname' prefix="ios-contact" @on-enter="handleSubmit" placeholder="Enter name" style="margin-top: 10px; width: 268px;"
                        />
                    </FormItem>
                    <!-- 密码 -->
                    <FormItem prop="password">
                        <Input v-model='formCustom.password' type="password" prefix="ios-alert" @on-enter="handleSubmit" placeholder="Enter password"
                            style="margin-top: 20px;width: 268px;" />
                    </FormItem>
                    <Button type="primary" style="margin-top: 20px;width: 268px;" @click="handleSubmit">登录</Button>
                </Form>
            </div>
        </Card>
    </div>
</template>

<script>
    import Cookies from 'js-cookie';
    export default {
        name: 'login',
        data() {
            return {
                formCustom: {
                    nickname: '',
                    password: ''
                },
                // 表单校验规则
                rules: {
                    nickname: [
                        { required: true, message: '账号不能为空', trigger: 'blur' }
                    ],
                    password: [
                        { required: true, message: '密码不能为空', trigger: 'blur' }
                    ]
                }
            }
        },
        methods: {
            handleSubmit() {
                // loginForm 对应 ref    valid是校验的结果 true/false
                this.$refs.loginForm.validate((valid) => {
                    if (valid) {
                        if (this.formCustom.nickname == 'zql' && this.formCustom.password == '123456') {
                            this.$router.push({ name: 'home' });
                            Cookies.set('userName', this.formCustom.nickname);
                        } else {                      
                            this.$Message.error('用户名不存在或者密码错误');
                        }
                    }
                })
            }
        }


    }
</script>

<style scoped>
    #login {
        width: 100%;
        height: 100%;
        background: url(../../assets/背景.png);
        position: relative;
    }

    .login-card {
        width: 400px;
        height: 360px;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 500px;
        right: 0;
        margin: auto;
        border-radius: 5px;
        background-color: rgba(46, 41, 41, 0.3);
        border: none;
    }

    .login-title {
        height: 60px;
        text-align: center;

    }

    .login-title>p {
        font-size: 20px;
        color: rgb(255, 255, 255);
        font-weight: 400;
        margin-top: 20px;
    }

    .title-img {
        text-align: center;
    }

    .login-form {
        width: 100%;
        height: 225px;
        text-align: center;

    }

    .login-form>input,
    .login-form>button {
        margin: auto
    }
</style>